
/****************************/
/* contact                  */
/****************************/
.contact {
    padding: 30px;
}
.ipt_wrap {
    margin-bottom: 20px;
    visibility: hidden;
    opacity: 0;
    transform: translateY(10px);
    transition: .3s;
    font-size: 1.375rem;
    font-weight: bold;

    input[type=text]{
        height:auto;
        min-width:30px;
        margin: 0 10px;
        padding: 0;
        border: 0;
        vertical-align: bottom;
        text-indent: 0;
        color:#888;
        font-size: 1.375rem;
        font-weight: bold;
        border-bottom: 1px solid #000;
        outline:none;
        transition: .1s;
    }
    textarea {
        padding: 0;
        text-indent: 0;
        font-size: 1.375rem;
        color:#888;
        border:0;
        border-bottom:1px solid #888;
        font-weight: bold;
        outline:none;
    }

    &.default,
    &.show{
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
    }
    &.entered{
        font-weight: bold;
        color:#888;

        input[type=text]{
            border-bottom: 1px solid #888;
        }

    }
    &.focus{
        color:#000;
        font-size: 1.875rem;
        * {
            font-weight: bold;
            color:#000;
            font-size: 1.875rem;
        }

        input[type=text],
        textarea {
            color:#000;
            font-size: 1.875rem;
        }
    }

}

.fake{
    min-width:30px;
    position:absolute;
    top:-50px;
    border: 1px solid red;
    display: inline-block;
    font-weight: bold;
    color:#aaa;
    font-size: 1.375rem;

    &.focus{
        color:#000;
        font-size: 1.875rem;   
    }
}

.btn_next{
    transition:.2s;
}